import React from "react";
import { Card, CardBody, Button, Modal, ModalBody } from "reactstrap";
import { Stepper } from "react-form-stepper";
import Papa from "papaparse";
import * as node from "./service/nodeService";
import Swal from "sweetalert2";
import { CSVDownload } from "react-csv";
// import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import { Oval } from "react-loader-spinner";

class Uploader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeStep: 0,
      uploadList: [],
      compareList: [],
      uploadDetails: "",
      compareDetails: "",
      uploadingFile: false,
      originalDataId: "",
      comparedDataId: "",
      matchComplete: false,
      matchData: [],
      matchDataHeaders: [],
    };
    this.uploadListRef = React.createRef(null);
    this.uploadCompareRef = React.createRef(null);
    this.handleFileListUpload = this.handleFileListUpload.bind(this);
  }

  handleListRefClick = () => {
    this.uploadListRef.current.click();
  };

  handleCompareRefClick = () => {
    this.uploadCompareRef.current.click();
  };

  handleFileListUpload = (e) => {
    this.setState({ uploadingFile: !this.state.uploadingFile });
    const files = e.target.files;

    this.setState({ uploadDetails: files[0].name });
    if (files) {
      Papa.parse(files[0], {
        complete: (results) => {
          node
            .originalData(JSON.stringify(results.data))
            .then((res) => {
              this.setState({
                originalDataId: res.originalDataId,
                uploadingFile: !this.state.uploadingFile,
              });
              Swal.fire({
                icon: "success",
                title: `${files[0].name} has been uploaded`,
              });
            })
            .catch((e) => console.log("ERROR", e));
        },
      });
    }
  };

  handleCompareFile = (e) => {
    this.setState({ uploadingFile: !this.state.uploadingFile });
    const files = e.target.files;

    this.setState({ compareDetails: files[0].name });
    if (files) {
      Papa.parse(files[0], {
        complete: (results) => {
          node
            .compareData(JSON.stringify(results.data))
            .then((res) => {
              this.setState({
                comparedDataId: res.compareDataId,
                uploadingFile: !this.state.uploadingFile,
              });
              Swal.fire({
                icon: "success",
                title: `${files[0].name} has been uploaded`,
              });
            })
            .catch((e) => console.log("ERROR", e));
        },
      });
    }
  };

  handleNextStep = () => {
    this.setState({ activeStep: this.state.activeStep + 1 });
  };

  handlePreviousStep = () => {
    this.setState({ activeStep: this.state.activeStep - 1 });
  };


  
  createMatchbackList = () => {
    this.setState({ uploadingFile: !this.state.uploadingFile });
    node
      .createNewList(this.state.originalDataId, this.state.comparedDataId)
      .then((res) => {
        console.log(res);
        console.log(res.shift());
        const data = res;
        const headers = res.shift();
        this.setState({
          matchDataHeaders: headers,
          matchData: data,
          matchComplete: true,
          uploadingFile: !this.state.uploadingFile,
        });
      });
  };

  handleOriginalFileRemove = () => {
    node.deleteOriginalFile(this.state.originalDataId);
    this.setState({ originalDataId: "", uploadDetails: "" });
  };

  handleCompareFileRemove = () => {
    node.deleteComparedFile(this.state.comparedDataId);
    this.setState({ comparedDataId: "", compareDetails: "" });
  };
  render() {
    return (
      <div className="container">
        <Modal size="sm" centered isOpen={this.state.uploadingFile}>
          <ModalBody centered className="text-center">
            <div className="text-center">
              <h2>Uploading File</h2>
              <Oval color="#00BFFF" height={200} width={350} />
            </div>
          </ModalBody>
        </Modal>
        <Card style={{ height: "550px" }}>
          <CardBody>
            <Stepper
              steps={[
                { label: "Upload List" },
                { label: "Upload CSV File to compare" },
                { label: "Create Matchback List" },
              ]}
              activeStep={this.state.activeStep}
            />
            <div className="col-12  text-center">
              <div>
                <h3>
                  {this.state.uploadDetails}
                  {""}{" "}
                  {this.state.uploadDetails ? (
                    <Button
                      onClick={() => this.handleOriginalFileRemove()}
                      className="btn-warning"
                    >
                      Delete
                    </Button>
                  ) : null}
                </h3>

                <br />
                <h3>
                  {this.state.compareDetails} {""}{" "}
                  {this.state.compareDetails ? (
                    <Button
                      onClick={() => this.handleCompareFileRemove()}
                      className="btn-warning"
                    >
                      Delete
                    </Button>
                  ) : null}
                </h3>
              </div>
              <br />
              {this.state.activeStep === 0 ? (
                <div>
                  <h5 style={{ color: "red" }}>
                    ** Please upload YOUR customer list first **{" "}
                  </h5>
                  <Button
                    onClick={this.handleListRefClick}
                    className="col-2 mt-5"
                    color="primary"
                    outline
                  >
                    Upload List
                  </Button>
                </div>
              ) : null}

              {this.state.activeStep === 1 ? (
                <div>
                  <h5 style={{ color: "red" }}>
                    ** Upload your postal list or list to compare to. **
                  </h5>
                  <Button
                    onClick={this.handleCompareRefClick}
                    className="col-2 mt-5"
                    color="primary"
                    outline
                  >
                    Upload List To Compare
                  </Button>
                </div>
              ) : null}

              {this.state.activeStep === 2 ? (
                <div>
                  <Button
                    onClick={this.createMatchbackList}
                    className="col-2 mt-5"
                    color="primary"
                    outline
                  >
                    Create Matchback List
                  </Button>
                  {this.state.matchComplete ? (
                    <CSVDownload
                      filename="MatchBack Results"
                      headers={this.state.matchDataHeaders}
                      data={this.state.matchData}
                    />
                  ) : null}
                </div>
              ) : null}
            </div>
          </CardBody>
          {this.state.activeStep === 0 ? (
            <input
              style={{ display: "none" }}
              // accept=".zip,.rar"
              ref={this.uploadListRef}
              onChange={this.handleFileListUpload}
              type="file"
            />
          ) : null}
          {this.state.activeStep === 1 ? (
            <input
              style={{ display: "none" }}
              // accept=".zip,.rar"
              ref={this.uploadCompareRef}
              onChange={this.handleCompareFile}
              type="file"
            />
          ) : null}

          <div className="col-12 row">
            <div className="col-6">
              <Button
                disabled={this.state.activeStep === 0}
                onClick={this.handlePreviousStep}
                color="danger"
                outline
              >
                Back
              </Button>
            </div>
            <div className="col-6">
              {" "}
              <Button
                disabled={this.state.activeStep === 2}
                onClick={this.handleNextStep}
                className="pull-right"
                color="success"
                outline
              >
                Next
              </Button>{" "}
            </div>
          </div>
          <div className="col-12"></div>
        </Card>
      </div>
    );
  }
}

export default Uploader;
