import axios from "axios";
import * as helpers from "./serviceHelpers";

const compareData = (data) => {
  const config = {
    method: "POST",
    url: `/node-api/compareData`,
    crossdomain: true,
    data,
    headers: { "Content-Type": "application/json" },
  };

  return axios(config)
    .then(helpers.onGlobalSuccess)
    .catch(helpers.onGlobalError);
};

const originalData = (data) => {
  const config = {
    method: "POST",
    url: `/node-api/originalData`,
    crossdomain: true,
    data,
    headers: {
      "Content-Type": "application/json",
    },
  };

  return axios(config)
    .then(helpers.onGlobalSuccess)
    .catch(helpers.onGlobalError);
};

const deleteOriginalFile = (id) => {
  const config = {
    method: "DELETE",
    url: `/node-api/delete-original/${id}`,
    crossdomain: true,

    headers: { "Content-Type": "application/json" },
  };

  return axios(config)
    .then(helpers.onGlobalSuccess)
    .catch(helpers.onGlobalError);
};

const deleteComparedFile = (id) => {
  const config = {
    method: "DELETE",
    url: `/node-api/delete-compared/${id}`,
    crossdomain: true,

    headers: { "Content-Type": "application/json" },
  };

  return axios(config)
    .then(helpers.onGlobalSuccess)
    .catch(helpers.onGlobalError);
};

const createNewList = (originalId, compareId) => {
  const config = {
    method: "GET",
    url: `/node-api/create-new-list/${originalId}/${compareId}`,
    crossdomain: true,

    headers: { "Content-Type": "application/json" },
  };

  return axios(config)
    .then(helpers.onGlobalSuccess)
    .catch(helpers.onGlobalError);
};

export {
  originalData,
  compareData,
  deleteOriginalFile,
  deleteComparedFile,
  createNewList,
};
